@use 'abstracts' as *;
@use 'base';


.container {
    background-color: darkblue;
    background-image: url('../components/Calculator/backgr.png');
    color: white;
    font-size: 10px;
    height: 100%;
    width: 100%;
}

.header {
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.main_page {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    max-width: 1300px;
    margin: 0 auto
}

.section_title {
    font-size: 4em;
    text-align: center;
    margin-bottom: 20px;
    margin-top: 60px;

    @include for-tablet() {
        text-align: center;
        font-size: 3em;
    }

    @include for-phone() {
        text-align: center;
        font-size: 2.5em;
    }

}

.section_description {
    font-size: 3em;
    text-align: center;
    margin: 0 50px;
    margin-bottom: 40px;

    @include for-tablet() {
        font-size: 2em;
        margin: 0 20px;
        margin-bottom: 30px;
    }

    @include for-phone() {
        font-size: 1.5em;
        margin-bottom: 20px;
    }
}

.footer {
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
}