@use '../../sass/abstracts/' as *;

.members-section {
  max-width: 1000px;
  margin: 0 auto;

  &__title {
    font-size: $font-size-extra-large;
    font-family: $font-family;
    color: $color-text-primary;
    padding-top: 10px;
    padding-bottom: 20px;
    text-align: center;
  }

  &__description {
    font-family: $font-family;
    font-size: $font-size-regular;
    color: $color-text-secondary;
    line-height: 20px;
    text-align: center;
    padding: 0 150px 20px 150px;
  }
}
